    // СКРОЛЛ ПО НАВИГАЦИИ

$(document).ready(function() {
  $(".top_nav").on("click", "a", function(event) {
    event.preventDefault();
    var id = $(this).attr("href"),
        top = $(id).offset().top;
    $("body,html").animate({scrollTop: top}, 1000);
  });
});

    // ПРЯЧЕТ МЕНЮ НАВИГАЦИИ

$(document).ready(function() {
        var menuBtn = $('.top-nav_btn');
        var menu = $('.top-nav_menu');
      
        menuBtn.on('click', function(event) {
          event.preventDefault();
          menu.toggleClass('active');
        });
      });

  // При нажатии на блок, добавляется класс

 var comp = $('.component > ul > li');
 
   comp.click(function (){
     $(this).toggleClass('component_active');
     $('.component > ul > li').not($(this)).removeClass('component_active');
 });

